@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap');
@import "reset";

* {
  font-family: 'Roboto', sans-serif;
  font-weight: normal;
}

*, *::after, *::before {
  box-sizing: border-box;
}

body {
  width: 100vw;
  margin: 0;
  padding: 0;

  background: #737373;

}

img{
  max-width: 100%;
  height: auto;
}

.container {

  max-width: 414px;
  padding: 0 20px;
  margin: 0 auto;

  @media (min-width: 480px) {
    max-width: 990px;
    padding: 0 40px;
  }

  @media (min-width: 990px) {
    max-width: 1423px;
    padding: 0 90px;
  }
}
