
.nav {

  &__body {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    width: 100%;
    height: 100%;
    padding: 30px 0 40px;

    background: #737373;
  }

  &__btn {
    color: #fff;

    &--close{

    }
  }

  &__links {
    display: flex;
    flex-direction: column;
  }

  &__item {
    position: relative;

    padding: 5px 0;
    margin-right: 40px;
    margin-bottom: 40px;

    font-style: normal;
    font-weight: normal;
    font-size: 23px;
    line-height: 27px;

    text-align: center;
    text-decoration: none;
    color: #fff;

    @media (min-width: 1440px) {
      margin-right: 74px;
      font-size: 1.2em;
    }

    &:first-letter {
      text-transform: uppercase;
    }

    &:last-child {
      margin-right: 0;
    }

    &::after {
      content: "";

      position: absolute;
      bottom: 0;
      left: 50%;

      display: block;
      width: 0;
      height: 2px;

      background: #fff;

      transition: all 0.5s ease;
    }

    &:focus {
      outline: none;
    }

    &:focus,
    &:hover {
      &::after {
        left: 0;
        width: 100%;

        transition: all 0.5s ease;
      }
    }
  }

  &__copy {
    font-style: normal;
    font-weight: 300;
    font-size: 15px;
    line-height: 15px;
    text-align: center;

    color: #FFFFFF;

    & p:first-child{
      margin-bottom: 30px;
    }

    a{
      text-decoration: none;
      color: #FFFFFF;
    }

    @media (min-width: 990px) {
      display: none;
    }
  }
}