
.slider{
  &__item{
    min-width: 274px;
    max-width: 387px;
    margin: 0 auto;
  }

  &__img{
    min-width: 274px;
    max-width: 387px;
    min-height: 338px;
    max-height: 478px;

    object-fit: cover;
    overflow: hidden;

    & img{
      transition: transform 1.5s;

      &:hover{
        transform: scale(1.1);
      }
    }
  }

  &__desc{
    padding: 25px 0;

    font-style: normal;
    font-weight: normal;
    font-size: 17px;
    line-height: 20px;
    text-align: center;

    color: #FFFFFF;

    @media (min-width: 480px) {
      font-size: 27px;
      line-height: 32px;
    }
  }
}