.facts{
  padding: 100px 60px 50px;

  .container{
    position: relative;
  }

  &__title{
    margin-bottom: 30px;

    font-style: normal;
    font-weight: normal;
    font-size: 35px;
    line-height: 41px;
    text-align: center;
    color: #FFFFFF;
  }

  //================== swiper=================

  & .swiper-pagination-bullet{
    width: 12px;
    height: 12px;
    margin: 0 8px;

    border: 1px solid #B78A3F;
    background: transparent;
    opacity: 1;

    transition: 0.5s;
  }

  & .swiper-pagination-bullet-active{
    background: #B78A3F;
  }

  & .swiper-button-prev::after,
  & .swiper-button-next::after {
    color: #B78A3F;
  }

  & .swiper-button-next,
  & .swiper-button-prev {
    display: none;

    @media (min-width: 990px) {
      display: block;
    }
  }
}