.sectors{
  padding: 50px 0;

  &__title{
    margin-bottom: 42px;

    font-style: normal;
    font-weight: normal;
    font-size: 27px;
    line-height: 32px;
    text-align: center;
    color: #FFFFFF;

    @media (min-width: 990px) {
      font-size: 35px;
      line-height: 41px;
    }
  }

  &__subtitle{
    display: none;
    margin-bottom: 42px;

    font-style: normal;
    font-weight: normal;
    font-size: 23px;
    line-height: 27px;

    color: #FFFFFF;

    @media (min-width: 990px) {
      display: block;
    }
  }

  &__list{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;

    margin-bottom: 60px;

    @media (min-width: 990px) {
      margin-bottom: 170px;
    }
  }

  &__item{
    position: relative;

    display: flex;
    align-items: center;
    justify-content: center;

    width: 360px;
    height: 240px;
    margin: 0 auto;
    margin-bottom: 33px;

    @media (min-width: 990px) {
      display: block;
      margin-bottom: 15px;
    }
  }

  &__name{
    display: block;
    width: 100%;

    font-style: normal;
    font-weight: normal;
    font-size: 23px;
    line-height: 27px;
    text-align: center;

    color: #222222;

    background: rgba(255, 255, 255, 0.83);

    transition: 0.5s;

    &:hover{
      cursor: pointer;
      color: #B78A3F;
    }
  }

  &__text{
    position: absolute;
    top: 0;

    padding: 16px 75px 16px 16px;

    font-style: normal;
    font-weight: normal;
    font-size: 19px;
    line-height: 22px;

    color: #000000;

    background: #DADADA;
    border: 0.3px solid #000000;

    pointer-events: none;
    appearance: none;

    transition: 2s;
  }
}