.partners{
  display: flex;
  width: 266px;
  height: 86px;

  @media (min-width: 990px) {
    width: 706px;
    height: 229px;
  }

  &__item{
    min-width: 70px;
    min-height: 70px;
    max-width: 100%;
    height: auto;
    margin-right: 25px;

    @media (min-width: 480px) {
      margin-right: 60px;
    }

    &:last-child{
      margin-right: 0;
    }
  }
}