.header{

  width: 100%;
  padding: 0 0 70px;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../../../assets/bg/header_bg.jpg");
  background-size: cover;

  @media (min-width: 480px) {
    padding-bottom: 178px;
  }

  &__wrap{
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 225px;

  }

  &__partners{

    @media (min-width: 990px) {
      margin-bottom: 0;
    }

  }

  &__nav{
    //display: none;
    //margin-bottom: 115px;

    @media (min-width: 990px) {
      //display: block;
    }
  }

  &__title{
    margin-bottom: 280px;

    font-size: 32px;
    line-height: 37px;

    color: #FFFFFF;

    @media (min-width: 480px) {
      margin-bottom: 125px;

      font-size: 48px;
      line-height: 52px;
    }

    @media (min-width: 990px) {
      margin-bottom: 125px;

      font-size: 57px;
      line-height: 67px;
    }

    @media (min-width: 780px) {
      width: 664px;
    }
  }

  &__btn{
    position: relative;

    display: inline-block;
    width: calc(100% + 40px);
    padding: 22px 0;
    margin-left: -20px;

    font-size: 28px;
    line-height: 37px;
    text-align: center;
    text-decoration: none;

    color: #FFFFFF;

    background: #B78A3F;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    transition: all 1s ease;
    overflow: hidden;

    @media (min-width: 480px) {
      width: auto;
      padding: 35px 151px;
      margin-left: 0;

      font-size: 32px;
    }

    &:after {
      content: " ";

      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;

      display: block;
      width: 100%;
      height: 100%;

      background: #fff;

      transform: scale(0);
      transition: all 0.5s ease;
    }

    &:focus,
    &:hover {
      z-index: 0;
      background: transparent;
      color: #B78A3F;

      &:after {
        transition: all 0.5s ease;
        background: #fff;
        transform: scale(1);
      }
    }
  }
}