.footer{
  border-top: 1px solid #FFFFFF;
  padding: 0 0 10px 0;

  &__wrap{
    @media (min-width: 990px) {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: space-between;
    }
  }

  &__logo{
    display: flex;
    justify-content: space-between;

    max-height: 80px;
    margin-bottom: 40px;

    @media (min-width: 990px) {
      width: 50%;
    }

    &-item{
      min-width: 80px;
      max-width: 140px;
    }
  }

  &__links{
    margin-bottom: 105px;

    @media (min-width: 990px) {
      width: 50%;
      margin-bottom: 75px;
    }

    &-item{
      display: block;
      margin-bottom: 10px;

      font-style: normal;
      font-weight: normal;
      font-size: 15px;
      line-height: 18px;
      text-align: center;
      text-decoration: none;

      color: #FFFFFF;

      @media (min-width: 990px) {
        margin-bottom: 20px;

        font-size: 17px;
        line-height: 20px;
        text-align: left;
      }

      &:first-letter {
        text-transform: uppercase;
      }

      &:last-child {
        margin-right: 0;
      }

      &:hover{
        color: #EEE851;
      }
    }
  }

  &__copy{

    font-family: HelveticaNeueCyr, Roboto, sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 10px;
    line-height: 10px;
    text-align: center;

    color: #FFFFFF;

    @media (min-width: 990px) {
      width: 50%;

      font-size: 14px;
      line-height: 14px;
      text-align: left;
    }

    & p{
      &:first-child{
        margin-bottom: 15px;
      }
    }

    & a{
      display: block;
      text-decoration: none;
      color: #FFFFFF;

      &:hover{
        color: #EEE851;
      }
    }
  }

  &__about{
    margin-bottom: 90px;

    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
    text-align: center;

    color: #FFFFFF;

    @media (min-width: 990px) {
      width: 366px;
      text-align: left;
    }

    &>span{
      color: #EEE851;
    }
  }
}