@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after{content:"";content:none}q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}*{font-family:'Roboto', sans-serif;font-weight:normal}*,*::after,*::before{box-sizing:border-box}body{width:100vw;margin:0;padding:0;background:#737373}img{max-width:100%;height:auto}.container{max-width:414px;padding:0 20px;margin:0 auto}@media (min-width: 480px){.container{max-width:990px;padding:0 40px}}@media (min-width: 990px){.container{max-width:1423px;padding:0 90px}}

.header{width:100%;padding:0 0 70px;background:linear-gradient(to top, rgba(0,0,0,0.5), rgba(0,0,0,0.5)),url(/static/media/header_bg.e7fd60ee.jpg);background-size:cover}@media (min-width: 480px){.header{padding-bottom:178px}}.header__wrap{display:flex;justify-content:space-between;align-items:center;margin-bottom:225px}@media (min-width: 990px){.header__partners{margin-bottom:0}}.header__title{margin-bottom:280px;font-size:32px;line-height:37px;color:#FFFFFF}@media (min-width: 480px){.header__title{margin-bottom:125px;font-size:48px;line-height:52px}}@media (min-width: 990px){.header__title{margin-bottom:125px;font-size:57px;line-height:67px}}@media (min-width: 780px){.header__title{width:664px}}.header__btn{position:relative;display:inline-block;width:calc(100% + 40px);padding:22px 0;margin-left:-20px;font-size:28px;line-height:37px;text-align:center;text-decoration:none;color:#FFFFFF;background:#B78A3F;-webkit-filter:drop-shadow(0px 4px 4px rgba(0,0,0,0.25));filter:drop-shadow(0px 4px 4px rgba(0,0,0,0.25));transition:all 1s ease;overflow:hidden}@media (min-width: 480px){.header__btn{width:auto;padding:35px 151px;margin-left:0;font-size:32px}}.header__btn:after{content:" ";position:absolute;top:0;left:0;z-index:-1;display:block;width:100%;height:100%;background:#fff;transform:scale(0);transition:all 0.5s ease}.header__btn:focus,.header__btn:hover{z-index:0;background:transparent;color:#B78A3F}.header__btn:focus:after,.header__btn:hover:after{transition:all 0.5s ease;background:#fff;transform:scale(1)}

.partners{display:flex;width:266px;height:86px}@media (min-width: 990px){.partners{width:706px;height:229px}}.partners__item{min-width:70px;min-height:70px;max-width:100%;height:auto;margin-right:25px}@media (min-width: 480px){.partners__item{margin-right:60px}}.partners__item:last-child{margin-right:0}

.nav__body{position:absolute;top:0;right:0;z-index:10;display:flex;flex-direction:column;justify-content:space-between;width:100%;height:100%;padding:30px 0 40px;background:#737373}.nav__btn{color:#fff}.nav__links{display:flex;flex-direction:column}.nav__item{position:relative;padding:5px 0;margin-right:40px;margin-bottom:40px;font-style:normal;font-weight:normal;font-size:23px;line-height:27px;text-align:center;text-decoration:none;color:#fff}@media (min-width: 1440px){.nav__item{margin-right:74px;font-size:1.2em}}.nav__item:first-letter{text-transform:uppercase}.nav__item:last-child{margin-right:0}.nav__item::after{content:"";position:absolute;bottom:0;left:50%;display:block;width:0;height:2px;background:#fff;transition:all 0.5s ease}.nav__item:focus{outline:none}.nav__item:focus::after,.nav__item:hover::after{left:0;width:100%;transition:all 0.5s ease}.nav__copy{font-style:normal;font-weight:300;font-size:15px;line-height:15px;text-align:center;color:#FFFFFF}.nav__copy p:first-child{margin-bottom:30px}.nav__copy a{text-decoration:none;color:#FFFFFF}@media (min-width: 990px){.nav__copy{display:none}}

.slider__item{min-width:274px;max-width:387px;margin:0 auto}.slider__img{min-width:274px;max-width:387px;min-height:338px;max-height:478px;object-fit:cover;overflow:hidden}.slider__img img{transition:transform 1.5s}.slider__img img:hover{transform:scale(1.1)}.slider__desc{padding:25px 0;font-style:normal;font-weight:normal;font-size:17px;line-height:20px;text-align:center;color:#FFFFFF}@media (min-width: 480px){.slider__desc{font-size:27px;line-height:32px}}

.facts{padding:100px 60px 50px}.facts .container{position:relative}.facts__title{margin-bottom:30px;font-style:normal;font-weight:normal;font-size:35px;line-height:41px;text-align:center;color:#FFFFFF}.facts .swiper-pagination-bullet{width:12px;height:12px;margin:0 8px;border:1px solid #B78A3F;background:transparent;opacity:1;transition:0.5s}.facts .swiper-pagination-bullet-active{background:#B78A3F}.facts .swiper-button-prev::after,.facts .swiper-button-next::after{color:#B78A3F}.facts .swiper-button-next,.facts .swiper-button-prev{display:none}@media (min-width: 990px){.facts .swiper-button-next,.facts .swiper-button-prev{display:block}}

.sectors{padding:50px 0}.sectors__title{margin-bottom:42px;font-style:normal;font-weight:normal;font-size:27px;line-height:32px;text-align:center;color:#FFFFFF}@media (min-width: 990px){.sectors__title{font-size:35px;line-height:41px}}.sectors__subtitle{display:none;margin-bottom:42px;font-style:normal;font-weight:normal;font-size:23px;line-height:27px;color:#FFFFFF}@media (min-width: 990px){.sectors__subtitle{display:block}}.sectors__list{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-between;margin-bottom:60px}@media (min-width: 990px){.sectors__list{margin-bottom:170px}}.sectors__item{position:relative;display:flex;align-items:center;justify-content:center;width:360px;height:240px;margin:0 auto;margin-bottom:33px}@media (min-width: 990px){.sectors__item{display:block;margin-bottom:15px}}.sectors__name{display:block;width:100%;font-style:normal;font-weight:normal;font-size:23px;line-height:27px;text-align:center;color:#222222;background:rgba(255,255,255,0.83);transition:0.5s}.sectors__name:hover{cursor:pointer;color:#B78A3F}.sectors__text{position:absolute;top:0;padding:16px 75px 16px 16px;font-style:normal;font-weight:normal;font-size:19px;line-height:22px;color:#000000;background:#DADADA;border:0.3px solid #000000;pointer-events:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;transition:2s}

.footer{border-top:1px solid #FFFFFF;padding:0 0 10px 0}@media (min-width: 990px){.footer__wrap{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:space-between}}.footer__logo{display:flex;justify-content:space-between;max-height:80px;margin-bottom:40px}@media (min-width: 990px){.footer__logo{width:50%}}.footer__logo-item{min-width:80px;max-width:140px}.footer__links{margin-bottom:105px}@media (min-width: 990px){.footer__links{width:50%;margin-bottom:75px}}.footer__links-item{display:block;margin-bottom:10px;font-style:normal;font-weight:normal;font-size:15px;line-height:18px;text-align:center;text-decoration:none;color:#FFFFFF}@media (min-width: 990px){.footer__links-item{margin-bottom:20px;font-size:17px;line-height:20px;text-align:left}}.footer__links-item:first-letter{text-transform:uppercase}.footer__links-item:last-child{margin-right:0}.footer__links-item:hover{color:#EEE851}.footer__copy{font-family:HelveticaNeueCyr, Roboto, sans-serif;font-style:normal;font-weight:300;font-size:10px;line-height:10px;text-align:center;color:#FFFFFF}@media (min-width: 990px){.footer__copy{width:50%;font-size:14px;line-height:14px;text-align:left}}.footer__copy p:first-child{margin-bottom:15px}.footer__copy a{display:block;text-decoration:none;color:#FFFFFF}.footer__copy a:hover{color:#EEE851}.footer__about{margin-bottom:90px;font-style:normal;font-weight:normal;font-size:15px;line-height:18px;text-align:center;color:#FFFFFF}@media (min-width: 990px){.footer__about{width:366px;text-align:left}}.footer__about>span{color:#EEE851}

.maps{padding:50px 0}.maps__title{text-align:center;font-weight:normal;font-size:35px;line-height:41px;color:#fff}.maps__map{height:560px;margin:42px 0}.maps__text{font-weight:normal;font-size:23px;line-height:27px;color:#fff}

